import * as util from "@/utils";
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import VChart from "vue-echarts";
import CompEchart from "@/components/CompEchart";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
} from "echarts/components";
import { getHistoryTime, getKline, getBestTime } from "@/api/history_api";
import {
  cleanFactor,
  cleanMeta,
  initHeaders2,
  ObjToArray,
  reMapData,
} from "@/utils";
import { notify } from "@/components/notification";
import InfoTimeline from "@/components/InfoTimeline";

const upColor = "#00da3c";
const downColor = "#ec0000";

use([CanvasRenderer, TitleComponent, TooltipComponent, LegendComponent]);
