  components: { InfoTimeline, VChart, CompEchart },
  created() {
    this.setEmpty();
    this.initHistory();
  },
  watch: {
    tabs(newTab) {
      if (newTab == 1) {
        getHistoryTime(this.id)
          .then((res) => {
            if (res.data.status === 200) {
              let headers = res.data.headers;
              let data = res.data.data;
              headers = cleanMeta(headers);
              initHeaders2(headers, this);
              reMapData(data, headers);
              this.$set(this, "stockInfo", data); //相似度大约还要处理
              notify("success", "历史数据更新成功");
            } else {
              this.setEmpty();
              this.$refs.klines.clear();
              this.$set(this, "KLineData", []);
              this.$set(this.KLineOption, "series", []);
              this.$set(this, "KLineOption", this.noDataKOption);
              this.$set(this.info, "time", "");
              this.$refs.comp.init();
              notify("error", "历史数据暂无数据");
            }
          })
          .catch();
      }
    },
  },
  methods: {
    clickTab() {
      console.log("tab");
    },
    initHistory() {
      this.id = this.$store.state.item.stock_id;
      this.obj = this.$store.state.item;
      this.initTarget();
    },
    initTarget() {
      this.id = this.$store.state.item.stock_id;
      let time = this.$store.state.selected;
      getBestTime(this.id, time)
        .then((res) => {
          if (res.data.status === 200) {
            let data = res.data.data;
            this.target = data[0];
            this.initInfo();
            this.changeComp(this.target["_time"]);
            this.setKlines(this.id, this.target["_time"]);
            notify("success", "历史数据更新成功");
          } else {
            this.setEmpty();
            notify("error", "历史数据暂无数据");
          }
        })
        .catch();
    },
    initInfo() {
      this.$set(this, "factors", []);
      let metaStock = JSON.parse(
        JSON.stringify(this.$store.state.metaData)
      ).filter((item) => item.stock_id === this.id)[0];
      cleanFactor(metaStock);
      for (let key in metaStock) {
        this.factors.push({
          [key]: `${this.target[key]}(当前${metaStock[key]})`,
        });
      }
    },
    setKlines(id, time) {
      getKline(id, time)
        .then((res) => {
          if (res.data.status === 200) {
            let kLinesData = [];
            for (let item of res.data.data) {
              let headers = cleanMeta(res.data.headers);
              let data = ObjToArray(item, headers);
              kLinesData.push(data);
            }
            this.KLineData = kLinesData;
            this.initKLineChart(this.$store.state.item.headers1);
          } else {
            this.$refs.klines.clear();
            this.$set(this, "KLineData", []);
            this.$set(this.KLineOption, "series", []);
            this.$set(this, "KLineOption", this.noDataKOption);
            console.log(this.KLineOption);
          }
        })
        .catch();
    },
    changeTarget(item) {
      this.target = item;
      let headers0 = this.headers[0].text;
      this.changeComp(this.target[headers0]);
      this.setKlines(this.id, this.target[headers0]);
      this.initInfo();
    },
